/* 전체 화면 배경 */
body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background: linear-gradient(135deg, #ffe4e6, #fad0c4);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color: #6d6875;
}

/* 컨테이너 스타일 */
.home-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

/* 로그인 박스 */
.login-box {
    background: white;
    padding: 2rem;
    border-radius: 16px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    text-align: center;
    width: 380px;
    transition: transform 0.3s ease;
}

.login-box:hover {
    transform: scale(1.02);
}

/* 제목 스타일 */
.title {
    font-size: 2rem;
    margin-bottom: 1.5rem;
    color: #b5838d;
}

/* 입력 필드 */
.input-group {
    margin-bottom: 1.5rem;
    text-align: left;
}

.input-group label {
    font-size: 1rem;
    color: #6d6875;
    display: block;
    margin-bottom: 0.5rem;
}

.input-group input {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-sizing: border-box;
    transition: all 0.3s ease;
}

.input-group input:focus {
    border-color: #ff9a9e;
    outline: none;
    box-shadow: 0 0 5px rgba(255, 154, 158, 0.5);
}

/* 옵션 스타일 */
.options {
    margin-bottom: 2rem;
    font-size: 0.9rem;
    color: #6d6875;
    text-align: left;
}

.options input {
    margin-right: 0.5rem;
}

/* 버튼 그룹 */
.button-group {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

/* 버튼 스타일 */
button {
    padding: 0.9rem;
    font-size: 1rem;
    font-weight: bold;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s, box-shadow 0.3s;
}

/* 로그인 버튼 */
.login-btn {
    background-color: #ff9a9e;
    color: white;
    box-shadow: 0 4px 8px rgba(255, 154, 158, 0.4);
}

.login-btn:hover {
    background-color: #f76c6c;
    box-shadow: 0 6px 12px rgba(247, 108, 108, 0.5);
}

/* 회원가입 버튼 */
.signup-btn {
    background-color: #ffe4e6;
    color: #ff9a9e;
    border: 1px solid #ff9a9e;
    box-shadow: 0 4px 8px rgba(255, 154, 158, 0.2);
}

.signup-btn:hover {
    background-color: #ff9a9e;
    color: white;
    box-shadow: 0 6px 12px rgba(255, 154, 158, 0.4);
}